<template>
  <el-card>
    <template #header>
      <div class="flex flex-row justify-between">
        <span>新增电影</span>
        <router-link class=" text-blue-500" :to="{name:'MovieInfoList'}">返回列表</router-link>
      </div>
    </template>
    <el-form
      label-width="120px"
      :model="formData"
      :rules="formDataRules"
      ref="formEl">
      <el-form-item label="电影名称" prop="movie_name">
        <el-input placeholder="请输入管理员名称" v-model="formData.movie_name" />
      </el-form-item>
      <el-form-item label="电影时长" prop="duration">
        <el-input placeholder="请输入电影时长" v-model="formData.duration" />
      </el-form-item>
      <el-form-item label="电影图片" prop="movie_pc">
        <el-upload
          class="upload-box w-[120px] h-[150px] border-dashed border border-gray-500 flex justify-center items-center"
          :action="baseURL + '/movie-info/uploadMoviePc'"
          :show-file-list="false"
          :on-success="uploadMoviePcSuccess"
          :before-upload="beforeMoviePcUpload"
          name="movie_pc">
          <img v-if="formData.movie_pc" :src="baseURL+formData.movie_pc" class="avatar" />
          <el-icon v-else :size="60">
            <PictureFilled />
          </el-icon>
        </el-upload>
      </el-form-item>
      <el-form-item label="上映时间" prop="publicTime">
        <el-date-picker
          v-model="formData.publicTime"
          type="date"
          placeholder="请选择上映日期"
          value-format="YYYY-MM-DD" />
      </el-form-item>
      <el-form-item label="电影类型" prop="tags">
        <el-input placeholder="请输入电影类型 ，用空格隔开" v-model="formData.tags"></el-input>
      </el-form-item>
      <el-form-item label="主演人员" prop="role">
        <el-input placeholder="请输入主演人员，用空格隔开" v-model="formData.role" />
      </el-form-item>
      <el-form-item label="电影评分" prop="movie_score">
        <el-input-number placeholder="请输入电影评分" v-model="formData.movie_score" :precision="1" :step="0.1" :max="9.9" />
      </el-form-item>
      <el-form-item label="电影简介" prop="movie_desc">
        <el-input placeholder="请输入电影简介" v-model="formData.movie_desc" type="textarea" rows="8" />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm" :loading="isSubmiting">保存数据</el-button>
        <el-button type="danger" @click="resetForm">重置表单</el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>
<script setup>
import { ref, reactive, inject } from "vue";
import { PictureFilled } from "@element-plus/icons-vue";
import { ElMessage } from "element-plus";
import API from "@/utils/API";
import { useRouter } from "vue-router";

const router = useRouter();
const baseURL = inject("baseURL");
const formData = reactive({
  movie_name: "",
  duration: "",
  movie_pc: "",
  movie_desc: "",
  publicTime: "",
  tags: [],
  role: "",
  movie_score: 0,
});

const formDataRules = {
  movie_name: [{ required: true, message: "请输入电影名称", trigger: "blur" }],
  duration: [
    { required: true, message: "请输入电影时长", trigger: "blur" },
    { pattern: /^\d+$/, message: "请输入正确的电影时长", trigger: "blur" },
  ],
  movie_pc: [{ required: true, message: "请输入电影海报", trigger: "blur" }],
  movie_desc: [{ required: true, message: "请输入电影描述", trigger: "blur" }],
  publicTime: [
    { required: true, message: "请输入电影上映时间", trigger: "blur" },
  ],
  tags: [{ required: true, message: "请输入电影标签", trigger: "blur" }],
  role: [{ required: true, message: "请输入电影角色", trigger: "blur" }],
  movie_score: [
    { required: true, message: "请输入电影评分", trigger: "blur" },
    {
      pattern: /^\d(\.\d+)?$/,
      message: "请输入正确的电影评分",
      trigger: "blur",
    },
  ],
};

const formEl = ref(null);

const uploadMoviePcSuccess = (result) => {
  formData.movie_pc = result.data;
  ElMessage.success("上传成功");
};

const beforeMoviePcUpload = (rawFile) => {
  if (!/^image\/(jpe?g|png|gif|svg|webp|bmp)$/.test(rawFile.type)) {
    ElMessage.error("请选择图片格式");
    return false;
  } else if (rawFile.size / 1024 / 1024 > 2) {
    ElMessage.error("图片大小不能超过2MB!");
    return false;
  }
  return true;
};

const submitForm = () => {
  formEl.value.validate((valid) => {
    if (valid) {
      saveData();
    } else {
      ElMessage.error("请将信息填写完整");
    }
  });
};

//保存数据
const isSubmiting = ref(false);
const saveData = () => {
  isSubmiting.value = true;
  API.movieInfo
    .add(formData)
    .then((res) => {
      ElMessage.success("添加成功");
      router.replace({ name: "MovieInfoList" });
    })
    .finally(() => {
      isSubmiting.value = false;
    });
};

const resetForm = () => {
  formEl.value.resetFields();
};
</script>
<style scoped lang="scss">
.upload-box {
  overflow: hidden;
  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}
</style>